@font-face {
  font-family: 'sheet';
  src: url("../../assets/fonts/sheet.woff2") format("woff2"), url("../../assets/fonts/sheet.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.sheet {
  --ss-color: #059669;
  --ss-duration: 1s;
  --ss-gap: 3em;
  --ss-meta-color: #fff;
  --ss-meta-title-size: 2.5em;
  --ss-meta-text-size: 1.2em;
  --ss-arrow-right: "\e828";
  --ss-arrow-left: "\e827";
  font-size: 14px;
  overflow: hidden;
  line-height: 1;
  position: relative;
}

.sheet input {
  display: none;
}

.sheet-content {
  display: flex;
  transform: translate3d(calc((var(--ss-pos,0) - 1) * -100%), 0, 0);
  transition: transform var(--ss-duration) var(--ss-timing, cubic-bezier(0.5, 0, 0.1, 1));
  will-change: transform;
}

.sheet-arrows label::before {
  font-family: "sheet";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-weight: normal;
  text-transform: none;
  display: inline-block;
  text-decoration: none;
  line-height: 1;
  text-align: center;
  vertical-align: middle;
}

.sheet-item {
  flex-shrink: 0;
  flex-basis: 100%;
  position: relative;
}

.sheet-item::after {
  display: block;
  content: "";
  padding-top: calc((var(--ss-ratio-y, 9) / var(--ss-ratio-x, 21)) * 100%);
}

.sheet-item img {
  width: 100%;
  display: block;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.sheet-meta {
  font-weight: 500;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1), 0 2px 5px rgba(0, 0, 0, 0.2);
  position: absolute;
  color: var(--ss-meta-color);
  top: var(--ss-gap);
  left: var(--ss-gap);
  opacity: var(--ss-i, 0);
  transform: translate3d(0, calc((var(--ss-i, 5) - 1) * 1em), 0);
  transition-delay: calc(var(--ss-duration) * .5);
  transition-duration: calc(var(--ss-duration) * .75);
  transition-property: transform, opacity;
  will-change: opacity, transform;
}

.sheet-meta * {
  margin-top: 0;
  margin-bottom: 0;
}

.sheet-meta *:first-child {
  font-size: var(--ss-meta-title-size);
}

.sheet-meta * + * {
  font-size: var(--ss-meta-text-size);
}

.sheet input:nth-child(1):checked ~ * {
  --ss-pos: 1;
}

.sheet input:nth-child(1):checked ~ * > :nth-child(1) {
  --ss-i: 1;
}

.sheet input:nth-child(2):checked ~ * {
  --ss-pos: 2;
}

.sheet input:nth-child(2):checked ~ * > :nth-child(2) {
  --ss-i: 1;
}

.sheet input:nth-child(3):checked ~ * {
  --ss-pos: 3;
}

.sheet input:nth-child(3):checked ~ * > :nth-child(3) {
  --ss-i: 1;
}

.sheet input:nth-child(4):checked ~ * {
  --ss-pos: 4;
}

.sheet input:nth-child(4):checked ~ * > :nth-child(4) {
  --ss-i: 1;
}

.sheet input:nth-child(5):checked ~ * {
  --ss-pos: 5;
}

.sheet input:nth-child(5):checked ~ * > :nth-child(5) {
  --ss-i: 1;
}

.sheet input:nth-child(6):checked ~ * {
  --ss-pos: 6;
}

.sheet input:nth-child(6):checked ~ * > :nth-child(6) {
  --ss-i: 1;
}

.sheet input:nth-child(7):checked ~ * {
  --ss-pos: 7;
}

.sheet input:nth-child(7):checked ~ * > :nth-child(7) {
  --ss-i: 1;
}

.sheet input:nth-child(8):checked ~ * {
  --ss-pos: 8;
}

.sheet input:nth-child(8):checked ~ * > :nth-child(8) {
  --ss-i: 1;
}

.sheet-arrows {
  display: flex;
  bottom: var(--ss-gap);
  right: var(--ss-gap);
  position: absolute;
}

.sheet-arrows label::before {
  content: var(--ss-r, var(--ss-l));
}

.sheet input:nth-child(1):checked ~ .sheet-arrows > :nth-child(2) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(2):checked ~ .sheet-arrows > :nth-child(1) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(2):checked ~ .sheet-arrows > :nth-child(3) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(3):checked ~ .sheet-arrows > :nth-child(2) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(3):checked ~ .sheet-arrows > :nth-child(4) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(4):checked ~ .sheet-arrows > :nth-child(3) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(4):checked ~ .sheet-arrows > :nth-child(5) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(5):checked ~ .sheet-arrows > :nth-child(4) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(5):checked ~ .sheet-arrows > :nth-child(6) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(6):checked ~ .sheet-arrows > :nth-child(5) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(6):checked ~ .sheet-arrows > :nth-child(7) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(7):checked ~ .sheet-arrows > :nth-child(6) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:nth-child(7):checked ~ .sheet-arrows > :nth-child(8) {
  --ss-r: var(--ss-arrow-right);
}

.sheet input:nth-child(8):checked ~ .sheet-arrows > :nth-child(7) {
  --ss-l: var(--ss-arrow-left);
}

.sheet input:checked:last-of-type:not(:nth-child(2)) ~ .sheet-arrows label:first-child:not(:only-child) {
  --ss-r: var(--ss-arrow-right);
  order: 8;
}

.sheet input:checked:first-of-type ~ .sheet-arrows label:last-child:not(:only-child) {
  --ss-l: var(--ss-arrow-left);
  order: -1;
}

.sheet-buttons {
  display: flex;
  gap: .25em;
  position: absolute;
  bottom: 0;
  left: var(--ss-gap);
  right: var(--ss-gap);
}

.sheet-buttons label {
  flex: 1 0 auto;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.25);
  transition: background-color calc(var(--ss-duration) * .5);
  will-change: background-color;
}

.sheet-buttons label::before {
  content: "";
  padding-bottom: 5px;
  display: block;
  background-color: var(--ss-color);
  opacity: var(--ss-i, 0);
  transition: opacity calc(var(--ss-duration) * .5);
  will-change: opacity;
}

.sheet-buttons label:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
